/* styles.css */
.id-card-generator {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Creates 4 equal-width columns */
  gap: 10px; /* Adjust the gap between items as needed */
}
.idCard {
  position: relative;
}
.id-card {
  margin: 10px;
  text-align: center;
}
.abs-name {
  position: absolute;
  bottom: 14%;
  left: 0;
  right: 0;
  width: 100%;
  text-align: center;
  z-index: 99;
}
.abs-name h3 {
  font-size: 18px;
  font-weight: 700;
  margin: 0;
  color: #fff;
}
.image-frame {
  width: 500px;
  position: relative;
}
.image-frame .img-temp {
  width: 500px;
}
.abs-load {
  position: absolute;
  bottom: 18%;
  width: 170px;
  height: 170px;
  left: 33%;
  z-index: 99;
}
.image-frame .user-image {
  position: absolute;
  bottom: 18%;
  width: 170px;
  height: 170px;
  left: 33%;
  z-index: 99;
}
.image-frame .user-image .img-user {
  display: block;
  height: 100%;
  width: 100%;
  border-radius: 70px;
}
.flex-wrap {
  display: flex;
  gap: 5px;
}
.u-img {
  width: 170px;
  height: 170px;
  border-radius: 70px;
  background-size: cover;
}
.wrapper {
  margin: 10px;
}
.form-control {
  display: block;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  color: #495057;
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  width: 100%;
}
input.form-control {
  width: 100px;
}
label {
  display: block;
  margin-bottom: 0.5rem;
}
